.photo__button-like {
    background-image: url('../../../images/like.svg');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    margin: 0;
    transition: all .5s ease;
    width: 22px;
    height: 19px;
    padding: 5px 5px;
    border: 0;
    background-color: #fff;
}

.photo__button-like:hover {
    opacity: 0.6;
    cursor: pointer;
}