.sign__button {
    border: none;
    border-radius: 2px;
    background: #fff;
    color: #000000;
    cursor: pointer;
    transition: ease 0.5s;
    width: 358px;
    height: 50px;
    font-size: 18px;
    line-height: 22px;
    text-align: center;
    margin-top: 216px;
    font-family: 'Inter', 'Arial', 'Helvetica', sans-serif;
}

.sign__button:hover {
    opacity: 0.8;
}